import {RootState} from "../store";
import {siteSettingsSliceName} from "./site-settings.reducer";

export const siteSettingsSelector = (state: RootState) => state[siteSettingsSliceName];
export const gigyaSettingsSelector = (state: RootState) => state[siteSettingsSliceName]?.gigyaSettings;
export const siteConfigSelector = (state: RootState) => state[siteSettingsSliceName]?.siteConfig;
export const siteConfigStatusSelector = (state: RootState) => state[siteSettingsSliceName].siteConfig?.status;
export const baseDomainsSelector = (state: RootState) => state[siteSettingsSliceName]?.siteConfig?.api?.baseDomains || [];
export const overrideFlagsSelector = (state: RootState) => state[siteSettingsSliceName]?.siteConfigOverrides?.flags || {};
export const originalFlagsSelector = (state: RootState) => state[siteSettingsSliceName]?.siteConfig?.flags || {};
export const dirtyFlagsSelector = (state: RootState) => {
    let dirtyFlags = [];
    const overrideFlags = overrideFlagsSelector(state);
    const originalFlags = originalFlagsSelector(state);

    for (const overrideFlagsKey in overrideFlags) {
        if (overrideFlags[overrideFlagsKey] !== originalFlags[overrideFlagsKey]) {
            dirtyFlags.push(overrideFlagsKey);
        }
    }

    return dirtyFlags;
};
