import {DesktopOutlined, CloudDownloadOutlined, PieChartOutlined, SettingOutlined} from "@ant-design/icons";
import {INavBarMenuItem} from "./Components/NavBar/NavBarInterfaces";

// Components
import LoadSitePage from "../LoadSitePage/LoadSitePage";
import SiteConfigPage from "../SiteSettingsPage/SiteConfigPage";
import {IRouterRoute} from "./Components/Router/RouterInterfaces";
import ScreenSetsPage from "../ScreenSetsPage/ScreenSetsPage";

export const NAVIGATE_TO = {
    HOME: '/',
    SCREEN_SETS: '/screen-sets',
    SITE_SETTINGS: '/settings',
    DATA: '/data',
}

export const appMenuItems: INavBarMenuItem[] = [
    {icon: <CloudDownloadOutlined />, text: 'Load Site', link: NAVIGATE_TO.HOME},
    {icon: <DesktopOutlined />, text: 'Screen Sets', link: NAVIGATE_TO.SCREEN_SETS},
    {icon: <SettingOutlined />, text: 'Settings', link: NAVIGATE_TO.SITE_SETTINGS},
    {icon: <PieChartOutlined />, text: 'Data', link: NAVIGATE_TO.DATA},
]

export const routes: IRouterRoute[] = [
    {path: '/', element: <LoadSitePage />},
    {path: '/screen-sets', element: <ScreenSetsPage />},
    {path: '/settings', element: <SiteConfigPage />},
    {path: '/data', element: <SiteConfigPage />},
]
