import React, {useEffect, useState} from "react";
import {Alert, Button, Form, Input, Select} from "antd";
import './GigyaLoaderForm.scss';
import {dataCenters, stagingEnv} from "./consts";
import {useAppDispatch, useAppSelector} from "../../../../Store/hooks";
import {
    baseDomainsSelector, gigyaSettingsSelector, siteConfigStatusSelector,
} from "../../../../Store/site-settings/site-seetings.selectors";
import {IGigyaSettings} from "../../../../Store/site-settings/site-settings.reducer";
import {getConfig} from "../../../../Store/AsyncActions/GigyaAsyncActions";

const {Option} = Select;

export interface IGigyaLoaderFormProps {
    submit: (data: IGigyaSettings) => void;
}
export default function GigyaLoaderForm({submit}: IGigyaLoaderFormProps) {
    const [form] = Form.useForm();
    const dispatch = useAppDispatch();
    const gigyaSettings = useAppSelector(gigyaSettingsSelector);
    const baseDomains = useAppSelector(baseDomainsSelector);
    const siteConfigStatus = useAppSelector(siteConfigStatusSelector);
    const [siteSettings, setSiteSettings] = useState<any>(gigyaSettings);

    const updateSettings = (key: string, value: any) => {
        setSiteSettings({
            ...siteSettings,
            [key]: value
        });
    }

    useEffect(() => {
        if (siteSettings?.apiKey && siteSettings?.dc) {
            dispatch(getConfig(siteSettings));
        }
    }, [siteSettings?.apiKey, siteSettings?.dc]);

    return (
        <Form className='gigya-loader-form' {...{labelCol: {span: 24}}} form={form} name="gigya-loader-form" onFinish={submit}
              layout='horizontal' validateMessages={{required: '${label} is required!'}} initialValues={siteSettings}>

            <Form.Item name="apiKey" label='API Key' rules={[{required: true}]}>
                <Input placeholder="Api Key" size="large"
                       onBlur={(e) => updateSettings('apiKey', e.target.value)}/>
            </Form.Item>
            <Form.Item name='dc' rules={[{required: true}]} label='Data Center'>
                <Select size="large" allowClear placeholder='datacenter'
                        onSelect={(value) => updateSettings('dc', value)}>
                    {dataCenters.map((dc, index) => <Option key={index} value={dc}>{dc}</Option>)}
                </Select>
            </Form.Item>
            {siteConfigStatus === 'failed' && <Alert type="error" message="Datacenter is not supported for this api key" banner/>}
            <Form.Item name='domain' label='Site Domain'>
                <Select size="large" allowClear placeholder='localhost' loading={siteConfigStatus === 'loading'}
                        onSelect={(value) => updateSettings('domain', value)}>
                    {baseDomains.map((dc, index) => <Option key={index} value={dc}>{dc}</Option>)}
                </Select>
            </Form.Item>
            <Form.Item name='stg' label='Staging Env'>
                <Select size="large" allowClear placeholder='e.g. st2'
                        onSelect={(value) => updateSettings('stg', value)}>
                    {stagingEnv.map((env, index) => <Option key={index} value={env}>{env}</Option>)}
                </Select>
            </Form.Item>
            <Form.Item wrapperCol={{offset: 10}}>
                <Button size='large' type="primary" htmlType="submit">
                    Load
                </Button>
            </Form.Item>
        </Form>
    );
}
