export type notificationsType = 'success' | 'error' | 'info' | 'warning' | 'warn' | 'open' | 'close'  | 'destroy' ;
export enum notify {
    success = 'success',
    error = 'error',
    info = 'info',
    warning = 'warning',
    warn = 'warn',
    open = 'open',
    close = 'close',
    destroy = 'destroy',
}
