import {Collapse, Tag} from "antd";
import ScreenSetParams from "./Components/ScreenSetParams/ScreenSetParams";
import Events from "./Components/Events/Events";
import Flags from "./Components/Flags/Flags";
import './ScreenSetsSettings.scss';
import {useAppDispatch, useAppSelector} from "../../../../Store/hooks";
import {renderScreenSetSelector, openedPanelsSelector} from "../../../../Store/screen-sets/screen-sets.selectors";
import {updatePanels, updateRender} from "../../../../Store/screen-sets/screen-sets.reducer";
import {RetweetOutlined} from "@ant-design/icons";
import {RenderType} from "../../../../Interfaces/ScreenSetsInterfaces";
const {Panel} = Collapse;

export default function ScreenSetsSettings() {
    const dispatch = useAppDispatch();
    const renderScreenSet = useAppSelector(renderScreenSetSelector);
    const panels = useAppSelector(openedPanelsSelector);

    const panelSelected = (panelsStatus: string | string[]) => {
        if (!Array.isArray(panelsStatus)) panelsStatus = [panelsStatus];
        dispatch(updatePanels(panelsStatus));
    }

    const switchView = (event: any) => {
        event.stopPropagation();
        if (renderScreenSet === RenderType.container) {
            dispatch(updateRender(RenderType.modal));
        }
        if (renderScreenSet === RenderType.modal) {
            dispatch(updateRender(RenderType.container));
        }
    }

    return (
        <Collapse onChange={panelSelected} defaultActiveKey={panels} className="screen-sets-settings">
            <Panel header="Screen Set Params" key="1"
                extra={<Tag onClick={switchView} icon={<RetweetOutlined />} color="#2db7f5">{renderScreenSet}</Tag>}>
                <ScreenSetParams />
            </Panel>
            <Panel header="Events" key="2">
                <Events />
            </Panel>
            <Flags key="3" header="Flags" />
        </Collapse>
    )
}
