import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {IScreenSetEvent, IScreenSetEventSettings, IScreenSetParam
} from "../../Components/ScreenSetsPage/Components/ScreenSetSettings/ScreenSetSettingsInterfaces";
import {GigyaEvent} from "../../Components/ScreenSetsPage/Components/ScreenSetSettings/Components/Events/EventsConsts";
import {IScreenSetData, RenderType} from "../../Interfaces/ScreenSetsInterfaces";
import {getScreenSets} from "../AsyncActions/GigyaAsyncActions";
import storage from "redux-persist/lib/storage";
import {PersistConfig} from "redux-persist/es/types";

export interface IScreenSetsState {
    screenSets: IScreenSetData[];
    openedPanels: string[];
    params: {[key: string]: any};
    events: {[key: string]: IScreenSetEventSettings};
    render: RenderType;
    status: 'idle' | 'loading' | 'failed';
}

const initialState: IScreenSetsState = {
    params: {lang: 'he', authFlow: 'popup'},
    events: {[GigyaEvent.onBeforeScreenLoad]: {toast: true}},
    screenSets: [],
    openedPanels: ['1', '2'],
    render: RenderType.modal,
    status: 'idle'
};

export const screenSetsSliceName = 'screen-sets';
export const screenSetsSlice = createSlice({
    name: screenSetsSliceName,
    initialState: initialState,
    reducers: {
        selectedScreenSet: (state: IScreenSetsState, action: PayloadAction<{screenSetId: string}>) => {
            return {...state, ...action.payload};
        },
        updateParams: (state: IScreenSetsState, action: PayloadAction<IScreenSetParam>) => {
            return {...state, params: {...state.params, [action.payload.paramKey]: action.payload.value}};
        },
        removeParam: (state: IScreenSetsState, action: PayloadAction<IScreenSetParam>) => {
            const newParams = {...state.params};
            delete newParams[action.payload.paramKey];
            return {...state, params: newParams};
        },
        updateEvents: (state: IScreenSetsState, action: PayloadAction<IScreenSetEvent>) => {
            return {...state, events: {...state.events, [action.payload.eventName]: action.payload.properties}};
        },
        removeEvent: (state: IScreenSetsState, action: PayloadAction<{eventName: GigyaEvent}>) => {
            const newEvents = {...state.events};
            delete newEvents[action.payload.eventName];
            return {...state, events: newEvents};
        },
        updatePanels: (state: IScreenSetsState, action: PayloadAction<string[]>) => {
            return {...state, openedPanels: action.payload};
        },
        updateRender: (state: IScreenSetsState, action: PayloadAction<RenderType>) => {
            return {...state, render: action.payload};
        },
    },
    extraReducers: (builder) => {
        builder.addCase(getScreenSets.pending, (state) => {
            return {...state, screenSets: [], status: 'loading'};
        })
        builder.addCase(getScreenSets.rejected, (state) => {
            return {...state, screenSets: [], status: 'failed'};
        })
        builder.addCase(getScreenSets.fulfilled, (state, action: PayloadAction<{screenSetData: IScreenSetData[]}>) => {
            return {...state, screenSets: action.payload.screenSetData, status: 'idle'};
        })
    }
});

export const { updateParams, removeParam, updateEvents, removeEvent, updatePanels, updateRender } = screenSetsSlice.actions;
export const screenSetsSlicePersistConfig: PersistConfig<IScreenSetsState> = {
    key: screenSetsSliceName,
    storage,
    blacklist: ['screenSets', 'status']
};
