import {IScreenSetData, RenderType} from "../../../../Interfaces/ScreenSetsInterfaces";
import Screen from "../Screen/Screen";
import './ScreenSet.scss';
import React, {useState} from "react";
import ScreenSetsSettings from "../ScreenSetSettings/ScreenSetSettings";
import {useAppSelector} from "../../../../Store/hooks";
import {renderScreenSetSelector} from "../../../../Store/screen-sets/screen-sets.selectors";
import {Button, Popover} from "antd";
import {SearchOutlined, SettingOutlined} from "@ant-design/icons";

// TODO: refactor with layout component
export default function ScreenSet(props: IScreenSetData) {
    const [visible, setVisible] = useState(false);
    const renderScreenSet = useAppSelector(renderScreenSetSelector);
    const visibleChanged = (value: boolean) => setVisible(value);

    const settings = () => <div className='screen-set-settings'><ScreenSetsSettings /></div>;
    const screens = () => {
        return (
            <div className='screen-list'>
                <div className='screen-list-items'>
                    {props.screens?.map((screen, index) =>
                        <Screen onSelectedScreen={visibleChanged} key={index} screenId={screen} screenSetId={props.screenSetID} />)}
                </div>
            </div>
        )
    };

    const renderModal = () => <div className="screen-set-modal">{screens()}{settings()}</div>
    const renderContainer = () => {
        return <div className="screen-set-container">
            <Popover placement="bottom" title='Screens' content={screens} onVisibleChange={visibleChanged} visible={visible} trigger="click"
                overlayInnerStyle={{width: '50rem', maxHeight: '48rem', overflow: 'auto'}}>
                <Button className='screen-set-container-btn' icon={<SearchOutlined />}>
                    Select A Screen
                </Button>
            </Popover>
            <Popover placement="bottom" title='Settings' content={settings} trigger="click"
                     overlayInnerStyle={{width: '50rem', maxHeight: '48rem', overflow: 'auto'}}>
                <Button className='screen-set-container-btn' icon={<SettingOutlined />}>Update Settings</Button>
            </Popover>
        </div>
    }

    return (
        <>{renderScreenSet === RenderType.modal ? renderModal() : renderContainer()}</>
    )
}
