import {createAsyncThunk} from "@reduxjs/toolkit";
import {fieldsToInclude, getSiteScreenSets} from "../../Services/screen-sets.service";
import {gigyaApi} from "../../Services/gigya-api.service";
import {GigyaEndpoints} from "../../Consts/gigyaApi";
import {IGigyaSettings} from "../site-settings/site-settings.reducer";
import {IGigyaScreenSets} from "../../Interfaces/ScreenSetsInterfaces";
import {IConfigResponse} from "../../Interfaces/GigyaConfig";
import {loadGigyaScript} from "../../Services/gigya-loader.service";

export const getScreenSets = createAsyncThunk('screenSets/getScreenSets',
    async (gigyaSettings: IGigyaSettings)  => {
        const data = await gigyaApi<IGigyaScreenSets>(
            GigyaEndpoints.getScreenSets, gigyaSettings, {include: fieldsToInclude});
        return {screenSetData: getSiteScreenSets(data.screenSets)};
    }
)

export const getConfig = createAsyncThunk('gigya/getConfig',
    async (gigyaSettings: IGigyaSettings) => {
        const data = await gigyaApi<IConfigResponse>(GigyaEndpoints.sdkConfigGet, gigyaSettings);
        return data;
    }
)

export const loadGigya = createAsyncThunk('gigya/loadGigyaJs',
    async (gigyaSettings: IGigyaSettings) => {
        await loadGigyaScript(gigyaSettings);
        return gigyaSettings;
    }
)
