import './EventItem.scss';
import {IScreenSetEventSettings} from "../../../ScreenSetSettingsInterfaces";
import {Card, Switch} from "antd";
import {DeleteOutlined} from "@ant-design/icons";
import {removeEvent, updateEvents} from "../../../../../../../Store/screen-sets/screen-sets.reducer";
import {useAppDispatch} from "../../../../../../../Store/hooks";
import _ from "lodash";
import {Events, GigyaEvent} from "../EventsConsts";

export interface IEventItemProps {
    eventName: GigyaEvent;
    eventSettings: IScreenSetEventSettings
}

export default function EventItem({eventName, eventSettings}: IEventItemProps) {
    const dispatch = useAppDispatch();
    const settingsChanged = (key: string, value: boolean) => {
        dispatch(updateEvents({eventName, properties: {...eventSettings, [key]: value}}));
    }
    const deleteEvent = () => {
        dispatch(removeEvent({eventName}));
    }
    return (
        <Card className='screen-set-event-item'
              size="small"
              title={_.find(Events, {eventName})?.eventText}
              extra={<DeleteOutlined className='screen-set-event-item__delete' onClick={deleteEvent}/>}
        >
            <div className='screen-set-event-item__settings'>
                <Switch className='screen-set-event-item__settings-item'
                        checkedChildren="Log"
                        onChange={(value) => settingsChanged('log', value)}
                        unCheckedChildren="Log"
                        defaultChecked={eventSettings.log}
                />
                <Switch
                    className='screen-set-event-item__settings-item'
                    onChange={(value) => settingsChanged('debug', value)}
                    checkedChildren="Debug"
                    unCheckedChildren="Debug"
                    defaultChecked={eventSettings.debug}
                />
                <Switch
                    className='screen-set-event-item__settings-item'
                    onChange={(value) => settingsChanged('toast', value)}
                    checkedChildren="Toast"
                    unCheckedChildren="Toast"
                    defaultChecked={eventSettings.toast}
                />
            </div>
        </Card>
    );
}
