import React from "react";
import './LoadSitePage.scss';
import GigyaLoaderForm from "./Components/GigyaLoaderForm/GigyaLoaderForm";
import {IGigyaSettings} from "../../Store/site-settings/site-settings.reducer";
import {NAVIGATE_TO} from "../App/AppConsts";
import {serviceRequest} from "../../Services/api.service";
import {ENDPOINTS} from "../../Consts/apiList";

export default function LoadSitePage() {
    const submit = async (data: IGigyaSettings) => {
        // TODO: remove this
        if (!data.domain || data.domain === 'localhost') {
            const params = `?apiKey=${data.apiKey}&dc=${data.dc}${data.stg ? `&stg=${data.stg}` : ''}&domain=localhost`;
            const url = `${NAVIGATE_TO.SCREEN_SETS}${params}`;
            return window.location.href = url;
        }
        return await serviceRequest({url: ENDPOINTS.siteSetup, params: data});
    }

    return (
        <div className='home-page'>
            <GigyaLoaderForm submit={submit} />
        </div>
    );
};
