import React, {useState} from "react";
import {Modal, notification} from "antd";
import {notificationsType} from "./ToasterConsts";
import _ from 'lodash';
import './Toaster.scss';
import {ArgsProps} from "antd/es/notification";
import { ReactComponent as GigyaSvg } from '../../Assets/gigya.svg';
import { ReactComponent as JsonFileSvg } from '../../Assets/json-file.svg';
import Icon from '@ant-design/icons';
import JsonViewer from "../JsonViewer/JsonViewer";

// TODO: move from here
const customIcons = {
    gigyaIcon: <Icon component={GigyaSvg} className='gigya-icon-style' />,
    jsonFileIcon: <Icon component={JsonFileSvg} />
}

export interface INotificationProps {
    type: notificationsType;
    message: string;
    description?: string;
    icon?: any;
    duration?: number;
    style?: any;
    data?: object;
}
// TODO: refactor
export default function Toast({type, message, description, icon, style, duration, data}: INotificationProps) {
    const args = {
        description, icon, duration, style,
        message: <NotificationMessage message={message} data={data} />,
        className: `applications-toast-${type}`,
        placement: 'bottomRight'
    };
    notification[type](_.omitBy(args, _.isNil) as ArgsProps & string);
}

interface INotificationMessage {
    message: string;
    data: any
}
// TODO: refactor
export function NotificationMessage({message, data}: INotificationMessage) {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const modelVisible = () => setIsModalVisible(!isModalVisible);

    return (
        <div className='notification-message'>
            <div>{message}</div>
            {data &&
                <div className='message-data' onClick={modelVisible}>
                    {customIcons.jsonFileIcon}
                </div>
            }
            <Modal className='message-data-modal' title={message} visible={isModalVisible} cancelText={'Close'} onCancel={modelVisible}>
                <JsonViewer data={data} />
            </Modal>
        </div>
    );
}
