import React from "react";
import ScreenSets from "./Components/ScreenSets/ScreenSets";
import {Button, Result} from "antd";
import {useAppSelector} from "../../Store/hooks";
import {gigyaSettingsSelector} from "../../Store/site-settings/site-seetings.selectors";
import {useNavigate} from "react-router-dom";
import {NAVIGATE_TO} from "../App/AppConsts";

export default function ScreenSetsPage() {
    const navigate = useNavigate();
    const gigyaSettings = useAppSelector(gigyaSettingsSelector);

    if (!gigyaSettings) {
        return <Result
            status="500"
            title="Some thing went wrong"
            subTitle="Sorry, You must load your site before accessing screen sets"
            extra={<Button type="primary" onClick={() => navigate(NAVIGATE_TO.HOME)}>Back Home</Button>}
        />
    }

    return(
        <ScreenSets gigyaSettings={gigyaSettings} />
    );
}
