import {combineReducers, configureStore} from "@reduxjs/toolkit";
import { persistReducer } from 'redux-persist';

import {siteSettingsSliceName, siteSettingsSlice, siteSettingsSlicePersistConfig} from "./site-settings/site-settings.reducer";
import {screenSetsSlice, screenSetsSliceName, screenSetsSlicePersistConfig} from "./screen-sets/screen-sets.reducer";
import {applicationSlice, applicationSliceName} from "./application/application.reducer";

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

const reducers = combineReducers({
    [applicationSliceName]: applicationSlice.reducer,
    [siteSettingsSliceName]: persistReducer(siteSettingsSlicePersistConfig, siteSettingsSlice.reducer),
    [screenSetsSliceName]: persistReducer(screenSetsSlicePersistConfig, screenSetsSlice.reducer)
});

export const store = configureStore({
    reducer: reducers,
    middleware: (getDefaultMiddleware) => getDefaultMiddleware({
        serializableCheck: false,
    }),
});
