import {Layout} from "antd";
import React, {useState} from "react";
import {MenuFoldOutlined, MenuUnfoldOutlined} from "@ant-design/icons";
import './ApplicationLayout.scss';
import {IApplicationLayoutProps} from "./ApplicationLayoutInterfaces";
import NavBar from "../NavBar/NavBar";
import { ReactComponent as Logo } from '../../../../Assets/sap-vector-logo.svg';

const { Header, Sider, Content } = Layout;

export default function ApplicationLayout({navigationItems, body, header}: IApplicationLayoutProps) {
    const [isCollapsed, toggleCollapsed] = useState(false);

    return (
        <Layout className='application-layout'>
            <Sider trigger={null} collapsible collapsed={isCollapsed}>
                <Logo className='logo' />
                <NavBar menuItems={navigationItems} />
            </Sider>
            <Layout className="site-layout">
                <Header className="header-layout">
                    {React.createElement(isCollapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                        className: 'trigger',
                        onClick: () => toggleCollapsed(!isCollapsed),
                    })}
                    {header}
                </Header>
                <Content className="content-layout">
                    {body}
                </Content>
            </Layout>
        </Layout>
    );
};
