import {Card, Popover} from "antd";
import {useState} from "react";
import {useAppDispatch, useAppSelector} from "../../../../../../Store/hooks";
import {updateParams} from "../../../../../../Store/screen-sets/screen-sets.reducer";
import {screenSetParamsSelector} from "../../../../../../Store/screen-sets/screen-sets.selectors";
import './ScreenSetParams.scss';
import {AppstoreAddOutlined} from "@ant-design/icons";
import ScreenSetParamForm from "./ScreenSetParamForm/ScreenSetParamForm";
import {IScreenSetParam} from "../../ScreenSetSettingsInterfaces";
import ScreenSetParamItem from "./ScreenSetParamItem/ScreenSetParamItem";

export default function ScreenSetParams() {
    const screenSetParams = useAppSelector(screenSetParamsSelector);
    const dispatch = useAppDispatch();
    const [visible, setVisible] = useState(false);
    const close = () => setVisible(false);
    const submit = (data: IScreenSetParam) => {
        dispatch(updateParams(data))
        close();
    };

    return (
        <div className='screen-set-params'>
            {Object.keys(screenSetParams).map((key) =>
                <ScreenSetParamItem key={key}  paramKey={key} value={screenSetParams[key]} />)}
            <Popover
                overlayInnerStyle={{borderRadius: '5px'}}
                destroyTooltipOnHide
                content={<ScreenSetParamForm cancel={close} submit={submit} />}
                trigger="click"
                placement="rightTop"
                visible={visible}
                onVisibleChange={() => setVisible(!visible)}
            >
                <Card size="small" className='screen-set-params__add'>
                    <AppstoreAddOutlined />
                </Card>
            </Popover>
        </div>
    )
}
