import {createSlice} from "@reduxjs/toolkit";
import {loadGigya} from "../AsyncActions/GigyaAsyncActions";
import Toast from "../../CommonComponents/Notification/Toaster";
import {notify} from "../../CommonComponents/Notification/ToasterConsts";
import {GIGYA_SETTINGS} from "../../Components/App/AppStrings";

export interface IApplicationState {
    isGigyaLoaded?: boolean;
    status: 'idle' | 'loading' | 'failed';
}

const initialState: IApplicationState = {
    isGigyaLoaded: false,
    status: 'idle',
};
export const applicationSliceName = 'app';
export const applicationSlice = createSlice({
    name: applicationSliceName,
    initialState: initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(loadGigya.pending, (state) => {
            Toast({type: notify.info, message: GIGYA_SETTINGS.LOAD_IN_PROGRESS});
            return {...state, status: 'loading'};
        })
        builder.addCase(loadGigya.fulfilled,
            (state) => {
                Toast({type: notify.success, message: GIGYA_SETTINGS.LOAD_SUCCESS});
                return {...state, isGigyaLoaded: true, status: 'idle'}
            })
        builder.addCase(loadGigya.rejected, (state) => {
            Toast({type: notify.error, message: GIGYA_SETTINGS.LOAD_FAILURE});
            return {...state, isGigyaLoaded: false, status: 'failed'};
        })
    },
});
