import React, {useEffect, useState} from 'react';
import {useNavigate, useLocation} from "react-router-dom";
import {Menu} from 'antd';
import _ from 'lodash';
import {INavBarProps} from "./NavBarInterfaces";
import './NavBar.scss';

export default function NavBar({menuItems}: INavBarProps) {
    const navigate = useNavigate();
    const location = useLocation();
    const [selectedItem, setSelectedItem] = useState<string>('0');

    useEffect(() => {
        setSelectedItem(_.findIndex(menuItems, (i) => i.link === location.pathname).toString());
    }, [navigate]);

    return (
        <Menu selectedKeys={[selectedItem]} mode="inline" theme="dark">
            {menuItems.map((item, index) => {
                return <Menu.Item key={index} icon={item.icon} onClick={() => navigate(item.link)}>{item.text}</Menu.Item>
            })}
        </Menu>
    );
}
