import {Button, Form, Select} from "antd";
import {Events} from "../EventsConsts";
import './EventItemForm.scss';
import {useAppSelector} from "../../../../../../../Store/hooks";
import {screenSetEventsSelector} from "../../../../../../../Store/screen-sets/screen-sets.selectors";

const {Option} = Select;

export interface IEventItemFormProps {
    submit: (eventName: string[]) => any;
}
export default function EventItemForm({submit}: IEventItemFormProps) {
    const screenSetEvents = useAppSelector(screenSetEventsSelector);
    const availableEvents = Events.filter(event => !screenSetEvents[event.eventName])
    const [form] = Form.useForm();
    const onSubmit = (eventName: {eventType: string[]}) => {
        submit(eventName.eventType);
        form.resetFields();
    }

    return (
        <Form form={form} className='event-item-form' name="event-item-form" onFinish={onSubmit}
              layout='horizontal' validateMessages={{required: 'Required!'}}>

            <Form.Item name='eventType' rules={[{required: true}]}>
                <Select placeholder='Event' mode="multiple">
                    {availableEvents.map(({eventText, eventName}: {eventText: string, eventName: string}) =>
                        <Option key={eventName} value={eventName}>{eventText}</Option>)}
                </Select>
            </Form.Item>
            <Form.Item>
                <div className='event-item-form__buttons'>
                    <Button type="primary" block htmlType="submit" ghost>Add</Button>
                </div>
            </Form.Item>
        </Form>
    );
}
