import './ScreenSetParamItem.scss';
import _ from "lodash";
import {screenSetParams} from "./ScreenSetParamItemConsts";
import {Card, Popover} from "antd";
import {SettingOutlined} from "@ant-design/icons";
import {useState} from "react";
import {useAppDispatch} from "../../../../../../../Store/hooks";
import {IScreenSetParam} from "../../../ScreenSetSettingsInterfaces";
import {removeParam, updateParams} from "../../../../../../../Store/screen-sets/screen-sets.reducer";
import ScreenSetParamForm from "../ScreenSetParamForm/ScreenSetParamForm";

export interface IScreenSetParamParams {
    paramKey: string;
    value: any;
}
export default function ScreenSetParamItem({paramKey, value}: IScreenSetParamParams) {
    const [visible, setVisible] = useState(false);
    const dispatch = useAppDispatch();

    const getParamFullName = () => _.find(screenSetParams, {paramKey})?.text || paramKey;
    const close = (data: IScreenSetParam) => {
        dispatch(removeParam(data));
        setVisible(false);
    };
    const submit = (data: IScreenSetParam) => {
        dispatch(updateParams(data));
        setVisible(false);
    };


    const editParamPopOver = () => {
        return <Popover
            overlayInnerStyle={{borderRadius: '5px'}}
            destroyTooltipOnHide
            content={<ScreenSetParamForm cancel={close} submit={submit} initialValue={{paramKey, value}} />}
            trigger="click"
            placement="rightTop"
            visible={visible}
            onVisibleChange={() => setVisible(!visible)}
        >
            <SettingOutlined className='screen-set-param__settings' />
        </Popover>
    }

    return (
        <Card className='screen-set-param'
              size="small"
              title={getParamFullName()}
              extra={editParamPopOver()}
        >
            <div>{value}</div>
        </Card>
    );
}
