import React, {useState} from "react";
import {Button, Form, Input, Select} from "antd";
import _ from 'lodash';
import './ScreenSetParamForm.scss';
import FactoryParamsComponents from "./FactoryParamsComponents";
import {screenSetParams} from "../ScreenSetParamItem/ScreenSetParamItemConsts";
import {IScreenSetFactoryParam, IScreenSetParam} from "../../../ScreenSetSettingsInterfaces";

const {Option} = Select;
export interface IScreenSetParamFormProps {
    cancel: (data: any) => any;
    submit: (data: IScreenSetParam) => any;
    initialValue?: IScreenSetParam
}
export default function ScreenSetParamForm({cancel, submit, initialValue}: IScreenSetParamFormProps) {
    const [form] = Form.useForm();
    const [selected, setSelected] = useState<IScreenSetFactoryParam | undefined>(
        _.find(screenSetParams, {paramKey: initialValue?.paramKey}));
    const disabledInput = <Form.Item><Input disabled placeholder='Value' /></Form.Item>;
    const onSubmit = (data: IScreenSetParam) => submit(data);
    const onCancel = () => cancel(initialValue);
    const onSelectedParam = (paramKey: string) => setSelected(_.find(screenSetParams, {paramKey}));

    return (
        <Form form={form} className='screen-set-param-form' name="screen-set-param-form" onFinish={onSubmit}
              layout='horizontal' validateMessages={{required: 'Required!'}}>

            <Form.Item name='paramKey' rules={[{ required: true }]} initialValue={initialValue?.paramKey}>
                <Select allowClear placeholder='Parameter' onSelect={onSelectedParam}>
                    {screenSetParams.map((param: IScreenSetFactoryParam) =>
                        <Option key={param.paramKey} value={param.paramKey}>{param.text}</Option>)}
                </Select>
            </Form.Item>

            { selected ? <FactoryParamsComponents param={selected} name='value' initialValue={initialValue?.value} /> : disabledInput}

            <Form.Item>
                <div className='screen-set-param-form-buttons'>
                    <Button type="primary" htmlType="submit" ghost>{initialValue?.paramKey ? 'Update' : 'Add'}</Button>
                    <Button danger onClick={onCancel}>{initialValue?.value ? 'Remove' : 'Cancel'}</Button>
                </div>
            </Form.Item>
        </Form>
    );
}
