import {Card} from "antd";
import Meta from "antd/es/card/Meta";
import './Screen.scss';
import {generateEventFunction, getScreenName} from "../../../../Utils/screenSet.utils";
import {useAppSelector} from "../../../../Store/hooks";
import {isGigyaLoadedSelector} from "../../../../Store/application/application.selectors";
import {
    renderScreenSetSelector,
    screenSetEventsSelector,
    screenSetParamsSelector
} from "../../../../Store/screen-sets/screen-sets.selectors";
import {RenderType} from "../../../../Interfaces/ScreenSetsInterfaces";

interface IScreenProps {
    screenId: string;
    screenSetId: string;
    onSelectedScreen: (visible: boolean) => void;
}
export default function Screen({screenId, screenSetId, onSelectedScreen}: IScreenProps) {
    const renderScreenSet = useAppSelector(renderScreenSetSelector);
    const isGigyaLoaded = useAppSelector(isGigyaLoadedSelector);
    const screenSetParams = useAppSelector(screenSetParamsSelector);
    const screenSetEvents = useAppSelector(screenSetEventsSelector);

    const screenSelected = () => {
        let events: any = {};
        onSelectedScreen(false);
        Object.keys(screenSetEvents).forEach(eventType => {
            events[eventType] = generateEventFunction(screenSetEvents[eventType]);
        });
        window.gigya.accounts.showScreenSet({
            screenSet: screenSetId,
            startScreen: screenId,
            ...screenSetParams,
            ...events,
            ...(renderScreenSet === RenderType.container && {containerID: 'gigya-screen-set-container-id'})
        });
    }

    return(
        <Card className='screen-item' onClick={screenSelected} hoverable loading={!isGigyaLoaded}>
            <Meta title={getScreenName(screenId)} description={`ID: ${screenId}`} />
        </Card>
    )
}
