import {IGigyaSettings} from "../Store/site-settings/site-settings.reducer";
import {createScriptElement} from "../Utils/script.utils";
import {gigyaUrl} from "../Utils/gigya.utils";

export function loadGigyaScript(gigyaSettings: IGigyaSettings): Promise<void> {
    const loadGigyaTimeOut = 10000;
    return new Promise((resolve, reject) => {
        const {apiKey, dc, stg} = gigyaSettings;
        if (!apiKey || !dc) {
           return resolve();
        }

        const timeoutId = window.setTimeout(() => reject(), loadGigyaTimeOut);
        window.onGigyaServiceReady = () => {
            window.gigya.accounts.addEventHandlers({
              onLogin: function (e: any) {
                console.log('GLOBAL accounts onLogin', e);
              },
            });
            window.clearTimeout(timeoutId);
            resolve();
        }

        const gigyaResourceUrl = `${gigyaUrl(dc, stg)}/js/gigya.js?apikey=${apiKey}&dbg=1`
        const gigyaScript = createScriptElement(gigyaResourceUrl);
        document.body.appendChild(gigyaScript);
    });
}
