import React from 'react';
import {Input, Select, Switch, Form} from "antd";
import {IScreenSetFactoryParam} from "../../../ScreenSetSettingsInterfaces";

const {Option} = Select;

interface IFactoryParamsComponentsProps {
    param: IScreenSetFactoryParam,
    name: string;
    initialValue?: any;
}

// TODO: refactor to Form Components/Common Components
export default function FactoryParamsComponents({param, name, initialValue}: IFactoryParamsComponentsProps) {
    switch (param.type) {
        case "number":
            return <Form.Item name={name} initialValue={initialValue}><Input placeholder={param.placeholder} /></Form.Item>;
        case "boolean":
            return <Form.Item name={name} initialValue={initialValue}><Switch defaultChecked /></Form.Item>;
        case "select":
            return <Form.Item name={name} initialValue={initialValue}><Select allowClear placeholder={param.placeholder}>
                       {param.options && param.options.map(option =>
                           <Option key={option} value={option}>{option}</Option>)}
                   </Select></Form.Item>;
        default:
            return <div>Something went wrong...</div>;
    }
}
