import {gigyaUrl} from "../Utils/gigya.utils";
import {GigyaEndpoints} from "../Consts/gigyaApi";
import {IGigyaResponse} from "../Interfaces/Gigya";
import {serviceRequest} from "./api.service";
import {IGigyaSettings} from "../Store/site-settings/site-settings.reducer";

export async function gigyaApi<T extends IGigyaResponse>(endpoint: GigyaEndpoints, gigyaSettings: IGigyaSettings, params?: any): Promise<T> {
    const {apiKey, dc, stg} = gigyaSettings;

    try {
        const res = await serviceRequest({
            url: `${gigyaUrl(dc, stg)}/${endpoint}`,
            params: {...params, httpStatusCodes: true, apiKey}
        });
        return res.data as T;
    } catch (e) {
        throw e;
    }
}
