import './Events.scss';
import {useAppDispatch, useAppSelector} from "../../../../../../Store/hooks";
import {screenSetEventsSelector} from "../../../../../../Store/screen-sets/screen-sets.selectors";
import EventItem from "./EventItem/EventItem";
import {Card, Popover} from "antd";
import {AppstoreAddOutlined} from "@ant-design/icons";
import {useState} from "react";
import EventItemForm from "./EventItemForm/EventItemForm";
import {updateEvents} from "../../../../../../Store/screen-sets/screen-sets.reducer";
import {GigyaEvent} from "./EventsConsts";

export default function Events() {
    const dispatch = useAppDispatch();
    const screenSetEvents = useAppSelector(screenSetEventsSelector);
    const [visible, setVisible] = useState(false);
    const close = () => setVisible(false);
    const addEvent = (eventNames: string[]) => {
        for (const eventName of eventNames) {
            dispatch(updateEvents({
                eventName,
                properties: {log: true, debug: false, toast: false}
            }));
        }
        close();
    };

    return (
        <div className='screen-set-events'>
            {Object.keys(screenSetEvents).map((event) =>
                <EventItem key={event} eventName={event as GigyaEvent} eventSettings={screenSetEvents[event]} />)}
            <Popover
                overlayInnerStyle={{borderRadius: '5px'}}
                destroyTooltipOnHide
                content={<EventItemForm submit={addEvent} />}
                trigger="click"
                placement="rightTop"
                visible={visible}
                onVisibleChange={() => setVisible(!visible)}
            >
                <Card size="small" className='screen-set-events__add'>
                    <AppstoreAddOutlined />
                </Card>
            </Popover>
        </div>
    );
}
