import React from "react";
import {Button, PageHeader} from "antd";
import './SiteInformationHeader.scss';
import {useAppSelector} from "../../Store/hooks";
import {isGigyaLoadedSelector} from "../../Store/application/application.selectors";
import SiteInformation from "./Components/SiteInformation/SiteInformation";
import {SettingOutlined} from "@ant-design/icons";
import {useNavigate} from "react-router-dom";
import {NAVIGATE_TO} from "../App/AppConsts";

export default function SiteInformationHeader() {
    const navigate = useNavigate();
    const gigyaLoaded = useAppSelector(isGigyaLoadedSelector);
    const subtitle = gigyaLoaded ? <SiteInformation /> : 'Site not loaded yet';
    const navigateToSettings = () => navigate(NAVIGATE_TO.SITE_SETTINGS);

    return (
        <PageHeader
            className='site-information-header'
            title="Site Information"
            subTitle={subtitle}
            extra={
                <Button disabled={!gigyaLoaded} onClick={navigateToSettings} icon={<SettingOutlined />}>Settings</Button>
            }
        />
    );
}
