import {useAppDispatch, useAppSelector} from "../../../../../../Store/hooks";
import {
    dirtyFlagsSelector,
    originalFlagsSelector,
    overrideFlagsSelector,
} from "../../../../../../Store/site-settings/site-seetings.selectors";
import {Spin, Switch, Tag, Collapse} from "antd";
import './Flags.scss';
import {updateOverrideFlags} from "../../../../../../Store/site-settings/site-settings.reducer";
import {useEffect, useState} from "react";
import {isGigyaLoadedSelector} from "../../../../../../Store/application/application.selectors";
import {SyncOutlined, ExclamationCircleOutlined, CheckCircleOutlined} from "@ant-design/icons";

export interface IFlagsProps {
    key: string;
    header: string;
}
export default function Flags({key, header, ...props}: IFlagsProps) {
    const dispatch = useAppDispatch();
    const gigyaLoaded = useAppSelector(isGigyaLoadedSelector);
    const overrideFlags = useAppSelector(overrideFlagsSelector);
    const originalFlags = useAppSelector(originalFlagsSelector);
    const dirtyFlags = useAppSelector(dirtyFlagsSelector);
    const gigyaFlags = Object.assign({}, originalFlags, overrideFlags);
    const [panelTag, setPanelTag] = useState<JSX.Element>();

    const flagChange = (flagKey: string) => {
        dispatch(updateOverrideFlags({flagName: flagKey, status: !gigyaFlags[flagKey]}))
    }

    // TODO: refactor
    useEffect((): void => {
        if (gigyaLoaded && window?.gigya?._?.config?.flags) {
            window.gigya._.config.flags = gigyaFlags;
            if (!dirtyFlags.length) {
                return setPanelTag(<Tag icon={<CheckCircleOutlined />} color="default">original</Tag>);
            } else {
                return setPanelTag(<Tag icon={<ExclamationCircleOutlined />} color="warning">modified</Tag>);
            }
        }
        return setPanelTag(<Tag icon={<SyncOutlined spin />} color="processing">processing</Tag>);
    }, [overrideFlags, gigyaLoaded]);

    return (
        <Collapse.Panel header={header} key={key} {...props} extra={panelTag}>
            <Spin spinning={!gigyaLoaded} delay={150}>
                <div className='gigya-flags'>
                    {Object.keys(gigyaFlags).map((flag, index) => {
                        return <Switch
                            key={index}
                            className={`gigya-flags-item ${dirtyFlags.includes(flag) && 'dirty'}`}
                            checkedChildren={flag}
                            onChange={() => flagChange(flag)}
                            unCheckedChildren={flag}
                            defaultChecked={gigyaFlags[flag]} />
                    })}
                </div>
            </Spin>
        </Collapse.Panel>

    )
}
