import _ from 'lodash';
import {getURLSearchParams} from "./url.utils";
import {IGigyaSettings} from "../Store/site-settings/site-settings.reducer";

export function gigyaUrl(dataCenter: string, staging?: string, namespace?: string): string {
    return `https://${getNameSpace(dataCenter)}.${gigyaDataCenter(dataCenter, staging)}.${gigyaBaseDomain(dataCenter)}`
}
export function gigyaDataCenter(dataCenter: string, staging?: string): string {
    return staging ? `${dataCenter}-${staging}` : dataCenter;
}
function gigyaBaseDomain(dataCenter?: string): string {
    if (dataCenter === 'cn1') {
        return 'sapcdm.cn';
    }
    return 'gigya.com';
}
export function getGigyaSettings(gigyaSettingsState: IGigyaSettings | undefined): any {
    const urlParams = getURLSearchParams<IGigyaSettings>();
    if (_.isEmpty(urlParams) || !urlParams.apiKey || !urlParams.dc) {
        return gigyaSettingsState;
    }

    return urlParams;
}
export function getNameSpace(dc: string) {
    return dc === 'us1' ? 'cdns' : 'accounts';
}
