import {IGigyaScreenSet, IScreenSetData} from "../Interfaces/ScreenSetsInterfaces";

export const fieldsToInclude = 'screenSetID,html,translations';
export function getSiteScreenSets(screenSets: Array<IGigyaScreenSet>): IScreenSetData[] {
    const screenSetsData: IScreenSetData[] = [];
    for (const screenSet of screenSets) {
        const {screenSetID, translations, html} = screenSet;
        screenSetsData.push({
            screenSetID,
            supportedLocales: Object.keys(translations),
            screens: getScreens(html)
        })
    }
    return screenSetsData;
}

export function getURL(apiKey: string): string {
    const include = ['screenSetID', 'html', 'translations'];
    return `/accounts.getScreenSets?apiKey=${apiKey}&include=${include.join(',')}`
}

function getScreens(html: string) {
    const div = document.createElement('div');
    div.innerHTML = html;
    const screens = Array.from(div.querySelectorAll('.gigya-screen') || [] );
    return screens.map(screen => screen.id);
}
