import {RootState} from "../store";
import {screenSetsSliceName} from "./screen-sets.reducer";

export const screenSetParamsSelector = (state: RootState) => state[screenSetsSliceName].params;
export const screenSetEventsSelector = (state: RootState) => state[screenSetsSliceName].events;
export const screenSetsSelector = (state: RootState) => state[screenSetsSliceName].screenSets;
export const openedPanelsSelector = (state: RootState) => state[screenSetsSliceName].openedPanels;
export const renderScreenSetSelector = (state: RootState) => state[screenSetsSliceName].render;
