import React from "react";
import {Divider} from "antd";
import './SiteInformation.scss';
import Paragraph from "antd/es/typography/Paragraph";

export default function SiteInformation() {
    return (
        <div className='site-information'>
            <Paragraph copyable={{ tooltips: true }}>{window.gigya?.apiKey}</Paragraph>
            <Divider type="vertical" />
            <div className='site-information-item'>{window.gigya?.dataCenter}</div>
            <Divider type="vertical" />
            <div className='site-information-item'>{window.gigya?.build?.number}</div>
        </div>
    );
}
