import React, {useEffect} from 'react';
import './App.scss';
import ApplicationLayout from "./Components/ApplicationLayout/ApplicationLayout";
import Router from "./Components/Router/Router";
import {useAppDispatch, useAppSelector} from "../../Store/hooks";
import {appMenuItems, NAVIGATE_TO, routes} from "./AppConsts";
import {gigyaSettingsSelector} from "../../Store/site-settings/site-seetings.selectors";
import {getGigyaSettings} from "../../Utils/gigya.utils";
import SiteInformationHeader from "../SiteInformationHeader/SiteInformationHeader";
import {getConfig, loadGigya} from "../../Store/AsyncActions/GigyaAsyncActions";
import {setGigyaSettings} from "../../Store/site-settings/site-settings.reducer";

export default function App() {
    const dispatch = useAppDispatch();
    const gigyaSettingsState = useAppSelector(gigyaSettingsSelector);
    const gigyaSettings = getGigyaSettings(gigyaSettingsState);

    useEffect(() => {
        if (gigyaSettings?.apiKey && gigyaSettings?.dc && gigyaSettings?.domain) {
            dispatch(setGigyaSettings(gigyaSettings));
            dispatch(getConfig(gigyaSettings));
            dispatch(loadGigya(gigyaSettings));
        }
    }, []);

    return (
        <ApplicationLayout
            navigationItems={appMenuItems}
            body={<Router routes={routes}/>}
            header={<SiteInformationHeader />}
        />
    );
}
