import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {IGigyaConfig} from "../../Interfaces/GigyaConfig";
import {getConfig} from "../AsyncActions/GigyaAsyncActions";
import {PersistConfig} from "redux-persist/es/types";
import storage from "redux-persist/lib/storage";

export interface IGigyaSettings {
    apiKey: string;
    dc: string;
    domain: string;
    stg?: string;
}
export interface IGigyaConfigState extends Partial<IGigyaConfig> {
    status?: 'idle' | 'loading' | 'failed';
}
export interface ISiteSettings {
    gigyaSettings?: IGigyaSettings;
    siteConfig?: IGigyaConfigState;
    siteConfigOverrides: Partial<IGigyaConfig>;
}

const initialState: ISiteSettings = {
    siteConfigOverrides: {flags: {}}
};

export const siteSettingsSliceName = 'site-settings';
export const siteSettingsSlice = createSlice({
    name: siteSettingsSliceName,
    initialState: initialState,
    reducers: {
        setGigyaSettings: (state: ISiteSettings, action: PayloadAction<IGigyaSettings>): ISiteSettings => {
            return {...state, gigyaSettings: action.payload}
        },
        updateOverrideFlags: (state: ISiteSettings, action: PayloadAction<{flagName: string, status: boolean}>): ISiteSettings => {
            return {
                ...state,
                siteConfigOverrides: {
                    flags: {...state.siteConfigOverrides.flags, [action.payload.flagName]: action.payload.status}
                }
            }
        },
    },
    extraReducers: (builder) => {
        builder.addCase(getConfig.pending, (state) => {
            return {...state, siteConfig: {status: 'loading'}};
        })
        builder.addCase(getConfig.fulfilled,
            (state, action: PayloadAction<IGigyaConfigState>) => {
                return {...state, siteConfig: {...action.payload, status: 'idle'}};
            })
        builder.addCase(getConfig.rejected, (state) => {
            return {...state, siteConfig: {status: 'failed'}};
        })
    }
});

export const { setGigyaSettings, updateOverrideFlags } = siteSettingsSlice.actions;
export const siteSettingsSlicePersistConfig: PersistConfig<ISiteSettings> = {
    key: siteSettingsSliceName,
    storage,
    blacklist: ['siteConfig']
};
