import {IScreenSetEventSettings} from "../Components/ScreenSetsPage/Components/ScreenSetSettings/ScreenSetSettingsInterfaces";
import chalk from "chalk";
import {eventNameToNotificationTypes,} from "../Components/ScreenSetsPage/Components/ScreenSetSettings/Components/Events/EventsConsts";
import Toast from "../CommonComponents/Notification/Toaster";

export function getScreenName(screenId: string): string {
    const screenParts = screenId.split('-');
    return  screenParts
        .map((part: any) => part !== 'gigya' && part !== 'screen' && part.charAt(0).toUpperCase() + part.slice(1))
        .filter((part: any) => part !== false)
        .join(' ');
}

export function gigyaEventsLogger(event: any): any {
    // TODO: improve logging
    console.log(chalk.white('Dispatched ' + chalk.cyan.underline.bold(event.eventName) + ' Event'), event);
}

export function gigyaEventsToaster(event: any): any {
    Toast({
        message: eventNameToNotificationTypes[event.eventName]?.text,
        type: (eventNameToNotificationTypes[event.eventName].notificationType),
        data: event
    });
}

export function generateEventFunction({log, toast, debug}: IScreenSetEventSettings): Function {
    window.gigyaEventsLogger = gigyaEventsLogger;
    window.gigyaEventsToaster = gigyaEventsToaster;
    const functionString = `${debug ? 'debugger;' : ''}${log ? 'window.gigyaEventsLogger(event);' : ''}${toast ? 'window.gigyaEventsToaster(event);' : ''}`;
    return new Function('event', functionString);
}

