import React, {useEffect} from "react";
import {PageHeader, Skeleton, Tabs} from "antd";
import './ScreenSets.scss';
import {useAppDispatch, useAppSelector} from "../../../../Store/hooks";
import {renderScreenSetSelector, screenSetsSelector} from "../../../../Store/screen-sets/screen-sets.selectors";
import ScreenSet from "../ScreenSet/ScreenSet";
import {getScreenSets} from "../../../../Store/AsyncActions/GigyaAsyncActions";
import {IGigyaSettings} from "../../../../Store/site-settings/site-settings.reducer";
import {RenderType} from "../../../../Interfaces/ScreenSetsInterfaces";

const { TabPane } = Tabs;

export interface IScreenSetsProps {
    gigyaSettings: IGigyaSettings;
}
export default function ScreenSets({gigyaSettings}: IScreenSetsProps) {
    const dispatch = useAppDispatch();
    const renderScreenSet = useAppSelector(renderScreenSetSelector);
    const screenSets = useAppSelector(screenSetsSelector);

    useEffect((): void => {
        dispatch(getScreenSets(gigyaSettings));
    }, [gigyaSettings]);

    if (!screenSets.length) {
        return <div>
            <PageHeader title="Loading Screen Sets..." />
            <Skeleton className='skeleton-style' paragraph={{ rows: 5 }} active />
        </div>
    }

    return (
        <>
            <Tabs type="card">
                {screenSets?.map((screenSetData) =>
                    <TabPane tab={screenSetData.screenSetID} key={screenSetData.screenSetID}>
                        <ScreenSet key={screenSetData.screenSetID} {...screenSetData} />
                    </TabPane>
                )}
            </Tabs>
            {renderScreenSet === RenderType.container &&
                <div id='gigya-screen-set-container-id' className='gigya-screen-set-container' />}
        </>

    );
};
