import {useAppSelector} from "../../../../Store/hooks";
import {gigyaSettingsSelector, siteConfigSelector} from "../../../../Store/site-settings/site-seetings.selectors";
import './SiteConfig.scss';
import {isGigyaLoadedSelector} from "../../../../Store/application/application.selectors";
import {Button, Result, Skeleton} from "antd";
import {NAVIGATE_TO} from "../../../App/AppConsts";
import React, {useState} from "react";
import {useNavigate} from "react-router-dom";
import JsonViewer from "../../../../CommonComponents/JsonViewer/JsonViewer";

const items = [
    {title: 'Environment', data: () => {
        return {
            build: window?.gigya?.build,
            dataCenter: window?.gigya?.dataCenter,
            storageDomainOverride: window?.gigya?.thisScript?.globalConf?.storageDomainOverride,
            customAPIDomainPrefix: window?.gigya?.partnerSettings?.customAPIDomainPrefix,
        }
    }},
    {title: 'SSO', data: (siteConfig: any) => siteConfig.sso},
    {title: 'API', data: (siteConfig: any) => siteConfig.api},
    {title: 'This Script', data: () => window?.gigya?.thisScript},
    {title: 'Partner Settings', data: () => window?.gigya?.partnerSettings},
];

export default function SiteConfig() {
    const [selected, setSelected] = useState(items[0]);
    const navigate = useNavigate();
    const gigyaSettings = useAppSelector(gigyaSettingsSelector);
    const isGigyaLoaded = useAppSelector(isGigyaLoadedSelector);
    const siteConfig = useAppSelector(siteConfigSelector);

    if (!gigyaSettings) {
        return <Result
            className='site-config'
            status="500"
            title="To view site config you must load your site first"
            extra={<Button type="primary" onClick={() => navigate(NAVIGATE_TO.HOME)}>Load Site</Button>}
        />
    }

    return (
        <div className='site-config'>
            <Skeleton loading={!isGigyaLoaded}>
                <div className='site-config__nav-bar'>
                    {items.map((item, index) =>
                        <div
                            className={`site-config__nav-bar__item ${selected.title === item.title ? 'selected' : ''}`}
                            key={index}
                            onClick={() => setSelected(item)}>
                            {item.title}
                        </div>
                    )}
                </div>
                <div className='site-config__data'>
                    <JsonViewer
                        data={selected?.data(siteConfig)}
                        customStyle={{overflowY: 'auto', borderRadius: '10px', backgroundColor: 'transparent'}} />
                </div>
            </Skeleton>
        </div>
    );
}
