import axios, {AxiosRequestConfig, AxiosResponse} from 'axios';

export async function serviceRequest(options: AxiosRequestConfig): Promise<any> {
    try {
        const res = await axios({...options, maxRedirects: 0});
        checkRedirect(res);
        return res;
    }
    // TODO: handle errors
    catch (e) {}
}

function checkRedirect(res: AxiosResponse) {
    if (res?.data?.redirect) {
        window.location.href = res?.data?.redirect
    }
}
